import React from 'react';
import {CopyToClipboard} from 'react-copy-to-clipboard';

const ContactCard = () => {
    return (
        <div className="contactCard">
            <h1>Restons en contact</h1>
            <ul>
                <li>
                    <i className="fas fa-map-marker-alt"></i>
                    <span>Montpellier</span>
                </li>
                <li>
                    <i className="fas fa-mobile-alt"></i>
                    <CopyToClipboard text="+33625937692">
                        <span className="clickInput" onClick = {() => {alert("Numéro de téléphone copié");}}>
                            (+33)6.25.93.76.92
                        </span>
                    </CopyToClipboard>
                </li>
                <li>
                    <i className="far fa-envelope"></i>
                    <CopyToClipboard text="mathieu.kint@hotmail.fr">
                    <span className="clickInput" onClick = {() => {alert("Adresse mail copiée");}}>
                        mathieu.kint@hotmail.fr
                    </span>
                    </CopyToClipboard>
                </li>
            </ul>
        </div>
    );
};

export default ContactCard;