import React from 'react';

const ContactSocialNetworks = () => {
    return (
        <div className="socialNetwork">
            <ul>
                <a href="https://www.linkedin.com/in/mathieukint" target="_blank" rel="noopener noreferrer">
                    <h4>LinkedIn</h4>
                    <i className="fab fa-linkedin" ></i>
                </a>
                <a href="https://github.com/mathieukint" target="_blank" rel="noopener noreferrer">
                    <h4>GitHub</h4>
                    <i className="fab fa-github" ></i>
                </a>
                <a href="https://twitter.com/mathieukint" target="_blank" rel="noopener noreferrer">
                    <h4>Twitter</h4>
                    <i className="fab fa-twitter" ></i>
                </a>
                <a href="https://www.wattpad.com/user/Mat_Young" target="_blank" rel="noopener noreferrer">
                    <h4>Wattpad</h4>
                    <i className="fas fa-book" ></i>
                </a>
            </ul>
        </div>
    );
};

export default ContactSocialNetworks;